@use 'vars';

.my-link {
    background-color: vars.$text-primary;
    border: 1px solid vars.$text-primary;
    display: flex;
    padding: 1vmax 3vmax;
    margin-bottom: 1.5vmin;
    border-radius: 1rem;
    text-decoration: none;
    transition: transform 250ms;
    width: 100%;

    &:hover {
        transform: scale(1.025);
    }

    .fa-icons {
        font-size: 2.5vmax;
    }

    span {
        margin: auto;
        color: vars.$dark-color;
        font-size: 2vmin;
        font-weight: bold;
    }
}