@use './vars';

.search-bar {
    width: 100%;
    position: relative;
    margin-bottom: 36px;
    text-align: center;

    h4 {
        font-size: 1.75vmax;
        margin: 8px;
        color: vars.$text-primary;
    }

    .search-input {
        border-radius: 1rem;
        width: 100%;
        height: 5vmax;
        text-align: center;
        position: relative;
        font-weight: bold;
        border-color: vars.$light-color;
        font-size: 1.5vmax;
        background-color: vars.$text-primary;

        &:focus+span {
            transform: translatey(250%);
            color: vars.$text-primary;
        }
    }

    .fa-search {
        position: absolute;
        color: vars.$dark-color;
        font-size: 2vmax;
        top: 68%;
        right: 2.5vmin;
        transform: translateY(-50%);

        &:hover {
            cursor: pointer;
        }
    }

    span {
        position: absolute;
        width: 100%;
        left: 0;
        top: 59%;
        font-size: 1vmax;
        font-weight: bold;
        transition: transform 250ms, color 250ms;
    }

}